import { Component, EventEmitter, Input, Output } from '@angular/core';
import {
  Acting,
  ActingErrorMessage,
  ActingErrorMessages
} from '@sofico-framework/utils';
import { Observable } from 'rxjs';

@Component({
  selector: 'sof-interactive-warning-dialog',
  template: `
    <sof-dialog-inner
      (destroy)="decline.emit()"
      [headerLabel]="tc + '.INTERACTIVE-WARNING-HEADER' | translate"
    >
      <div sof-dialog-body>
        <div class="mb-3" *ngFor="let error of actingErrorMessages$ | async">
          <sof-alert type="danger">
            {{
              error?.translation
                ? error?.translation
                : (tc + '.' + error?.message | translate: error?.messageParams)
            }}
          </sof-alert>
        </div>
        {{ tc + '.INTERACTIVE-WARNING-BODY' | translate }}
        <ul>
          <li *ngFor="let error of errors">{{ error }}</li>
        </ul>
      </div>
      <div
        sof-dialog-footer
        class="d-flex justify-content-end align-items-center"
      >
        <button
          sofButton
          sofFocus
          class="btn btn-primary btn-min-width order-1"
          (click)="confirm.emit()"
          [loading]="acting$ | async"
          [disabled]="acting$ | async"
        >
          {{ tc + '.CONTINUE' | translate }}
        </button>
        <button
          sofButton
          class="btn btn-outline-primary btn-min-width mr-2 order-0"
          (click)="decline.emit()"
          [disabled]="acting$ | async"
        >
          {{ tc + '.CANCEL' | translate }}
        </button>
      </div>
    </sof-dialog-inner>
  `,
  styleUrls: ['./confirm-dialog.component.scss']
})
export class InteractiveWarningDialogComponent {
  @Input() errors: string[];
  @Input() tc: string;
  @Input() bodyLabel: string;
  @Output() decline = new EventEmitter();
  @Output() confirm = new EventEmitter();

  @Acting() acting$: Observable<boolean>;
  @ActingErrorMessages() actingErrorMessages$: Observable<ActingErrorMessage[]>;
}
