import { Component, EventEmitter, Input, Output } from '@angular/core';
import {
  Acting,
  ActingErrorMessage,
  ActingErrorMessages
} from '@sofico-framework/utils';
import { Observable } from 'rxjs';

@Component({
  selector: 'sof-acknowledge-dialog',
  template: `
    <sof-dialog-inner
      (destroy)="acknowledge.emit()"
      [headerLabel]="
        disableHeaderLabelTranslation
          ? headerLabel
          : (tc + '.' + headerLabel | translate)
      "
    >
      <div sof-dialog-body>
        <div *ngFor="let error of actingErrorMessages$ | async" class="mb-3">
          <sof-alert type="danger">
            {{
              error?.translation
                ? error?.translation
                : (tc + '.' + error?.message | translate: error?.messageParams)
            }}
          </sof-alert>
        </div>
        {{
          disableBodyLabelTranslation
            ? bodyLabel
            : (tc + '.' + bodyLabel | translate)
        }}
      </div>
      <div sof-dialog-footer class="button-wrapper">
        <button
          sofButton
          sofFocus
          class="btn btn-primary btn-min-width order-1"
          [loading]="acting$ | async"
          [disabled]="acting$ | async"
          (click)="acknowledge.emit()"
        >
          {{ tc + '.' + acknowledgeLabel | translate }}
        </button>
      </div>
    </sof-dialog-inner>
  `,
  styleUrls: ['./acknowledge-dialog.component.scss']
})
export class AcknowledgeDialogComponent {
  @Input() headerLabel: string;
  @Input() acknowledgeLabel: string;
  @Input() tc: string;
  @Input() bodyLabel: string;
  @Input() disableHeaderLabelTranslation: boolean;
  @Input() disableBodyLabelTranslation: boolean;

  @Output() acknowledge = new EventEmitter();

  @Acting() acting$: Observable<boolean>;
  @ActingErrorMessages() actingErrorMessages$: Observable<ActingErrorMessage[]>;
}
