import { Component, EventEmitter, Input, Output } from '@angular/core';
import {
  Acting,
  ActingErrorMessage,
  ActingErrorMessages
} from '@sofico-framework/utils';
import { Observable } from 'rxjs';

@Component({
  selector: 'sof-confirm-dialog',
  template: `
    <sof-dialog-inner
      (destroy)="decline.emit()"
      [headerLabel]="tc + '.' + headerLabel | translate: labelParams"
    >
      <div sof-dialog-body>
        <div *ngFor="let error of actingErrorMessages$ | async" class="mb-3">
          <sof-alert type="danger">
            {{
              error?.translation
                ? error?.translation
                : (tc + '.' + error?.message | translate: error?.messageParams)
            }}
          </sof-alert>
        </div>
        {{ tc + '.' + bodyLabel | translate: labelParams }}
      </div>
      <div sof-dialog-footer class="button-wrapper">
        <ng-container *ngIf="primaryAction === 'confirm'; else otherAction">
          <button
            sofButton
            sofFocus
            class="btn btn-primary btn-min-width order-1"
            (click)="confirm.emit()"
            [loading]="!disableActing && (acting$ | async)"
            [disabled]="!disableActing && (acting$ | async)"
          >
            {{ tc + '.' + confirmLabel | translate }}
          </button>
          <button
            sofButton
            class="btn btn-outline-primary btn-min-width mr-2 order-0"
            (click)="decline.emit()"
            [disabled]="!disableActing && (acting$ | async)"
          >
            {{ tc + '.' + cancelLabel | translate }}
          </button>
        </ng-container>
        <ng-template #otherAction>
          <button
            sofButton
            sofFocus
            class="btn btn-primary btn-min-width order-1"
            (click)="decline.emit()"
            [disabled]="acting$ | async"
          >
            {{ tc + '.' + cancelLabel | translate }}
          </button>
          <button
            sofButton
            class="btn btn-outline-primary btn-min-width mr-2 order-0"
            (click)="confirm.emit()"
            [loading]="!disableActing && (acting$ | async)"
            [disabled]="!disableActing && (acting$ | async)"
          >
            {{ tc + '.' + confirmLabel | translate }}
          </button>
        </ng-template>
      </div>
    </sof-dialog-inner>
  `,
  styleUrls: ['./confirm-dialog.component.scss']
})
export class ConfirmDialogComponent {
  @Input() headerLabel: string;
  @Input() cancelLabel: string;
  @Input() confirmLabel: string;
  @Input() disableActing: boolean;
  @Input() tc: string;
  @Input() bodyLabel: string;
  @Input() primaryAction: 'confirm' | 'cancel';
  @Input() labelParams: { [key: string]: string };

  @Output() decline = new EventEmitter();
  @Output() confirm = new EventEmitter();

  @Acting() acting$: Observable<boolean>;
  @ActingErrorMessages() actingErrorMessages$: Observable<ActingErrorMessage[]>;
}
