import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { CdkPortal } from '@angular/cdk/portal';
import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { DialogConfigService } from './services/dialog-config.service';

/**
 * This component is the inline template way of working with dialogs. This means it can be
 * consumed by the `sof-dialog` selector.
 * It has a placeholder for the body called `sof-dialog-body` and a placeholder
 * for the footer called `sof-dialog-footer`
 */
@Component({
  selector: 'sof-dialog',
  template: `
    <ng-template cdkPortal>
      <sof-dialog-inner
        [icon]="icon"
        [sizeHeaderIcon]="sizeHeaderIcon"
        (destroy)="destroy.emit()"
        [headerLabel]="headerLabel"
        [hideDestroy]="hideDestroy"
      >
        <ng-content sof-dialog-body select="[sof-dialog-body]"></ng-content>
        <ng-content sof-dialog-footer select="[sof-dialog-footer]"></ng-content>
      </sof-dialog-inner>
    </ng-template>
  `,
  styleUrls: ['./dialog.component.scss']
})
export class DialogComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild(CdkPortal) portal;
  /**
   * The label that is shown in the header.
   * For consistency reasons we only want plain text in here rather than html
   */
  @Input() headerLabel: string;

  /**
   * The size of the dialog.
   * Can be sm - md - lg - xl.
   * sm by default
   */
  @Input() size: 'sm' | 'md' | 'lg' | 'xl' = 'sm';

  /**
   * The size of the dialog header icon.
   * 16 by default
   */
  @Input() sizeHeaderIcon:
    | '8'
    | '12'
    | '16'
    | '20'
    | '24'
    | '28'
    | '32'
    | '48' = '16';

  /**
   * The icon in the header of the dialog.
   */
  @Input() icon: string;

  /**
   * Hide the cross in the right top corner that emits on the destroy output
   * False by default
   */
  @Input() hideDestroy: boolean;

  /**
   * Output that is triggered when the close icon is clicked
   */
  @Output() destroy = new EventEmitter();

  overlayRef: OverlayRef;

  constructor(
    private overlay: Overlay,
    private dialogConfigService: DialogConfigService
  ) {}

  ngOnInit(): void {
    switch (this.size) {
      case 'sm':
        this.dialogConfigService.overlayConfig.width = '600px';
        break;
      case 'md':
        this.dialogConfigService.overlayConfig.width = '800px';
        break;
      case 'lg':
        this.dialogConfigService.overlayConfig.width = '1000px';
        break;
      case 'xl':
        this.dialogConfigService.overlayConfig.width = '1200px';
        break;
      default:
        this.dialogConfigService.overlayConfig.width = '600px';
    }

    this.overlayRef = this.overlay.create(
      this.dialogConfigService.overlayConfig
    );
  }

  ngAfterViewInit(): void {
    this.overlayRef.attach(this.portal);
  }

  ngOnDestroy(): void {
    this.overlayRef.detach();
    this.overlayRef.dispose();
  }
}
